@import url(https://fonts.googleapis.com/css?family=Special+Elite&display=swap);
@import url(https://fonts.googleapis.com/css?family=Special+Elite&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

:root {
  --main-green: #00F021;
  --main-blue: #00136D;
  --main-yellow: #FFD800;
}
.grid-container-mobile {
  display: grid;
  grid-template-areas:
    'title title title title title title'
    'yellow-bar yellow-bar yellow-bar yellow-bar yellow-bar yellow-bar'
    'sam-for-iowa sam-for-iowa sam-for-iowa sam-for-iowa sam-for-iowa sam-for-iowa'
    'yellow-bar-two yellow-bar-two yellow-bar-two yellow-bar-two yellow-bar-two yellow-bar-two'
    'short-bio short-bio short-bio short-bio short-bio short-bio'
    'social social social social social social'
    'cow-logo cow-logo cow-logo cow-logo cow-logo cow-logo';

  grid-gap: 20px;
  padding: 30px;
  font-family: 'Special Elite', cursive;
}

.grid-container-mobile > div {
  text-align: center;
}

.title-mobile { 
  grid-area: title;
  color: white;
  font-size: 30px;
}

.title-left-align-mobile{
  display: inline-block;
  text-align: left;
}

.yellow-bar-mobile{
  grid-area: yellow-bar;
  height: 15px;
  background-color: #FFD800;
  background-color: var(--main-yellow);
}

.sam-for-iowa-mobile{
  grid-area: sam-for-iowa;
  width: 100%;
}

.sam-for-iowa-mobile > img{
  width: 100%;
  height: auto;
}

.yellow-bar-two-mobile{
  grid-area: yellow-bar-two;
  height: 15px;
  background-color: #FFD800;
  background-color: var(--main-yellow);
}

.short-bio-mobile{
  grid-area: short-bio;
  color: white;
  font-size: 20px;
}

.social-mobile{
  grid-area: social;
  color: #00136D;
  color: var(--main-blue);
  background-color: #00F021;
  background-color: var(--main-green);
  font-size: 20px;
}

.cow-logo-mobile{
  grid-area: cow-logo;
  width: 100%;
}

.cow-logo-mobile > img{
  width: 100%;
  height: auto;
}

a, a:visited{
  color: #00136D;
  color: var(--main-blue);
}





.header-image{
  width: 100%;
}

.header-image > img{
  width: 100%;
  height: auto;
  object-fit: cover;
}
